.container-menu {
  width: 50%;
  padding-left: 10.55%;

  // @media (max-width:1139px) {
  //   padding-left: 18%;
  // }

  &__title {
    margin-top: 150px;

    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 71.7px;
    line-height: 72px;

    letter-spacing: 0.02em;

    color: #000;
    width: 30%;

    @media (max-width:1139px) {
      font-size: 48px;
      line-height: 48px;
    }

    @media (max-width: 767px) {
      font-size: 42px;
      line-height: 42px;
    }

    @media (max-width:665px) {
      display: none;
    }
  }
}

.container-menu-size {
  height: 100px;
  margin-top: 30px;
  display: flex;

  &__text {
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 13.9px;
    line-height: 100%;
    text-align: right;
    letter-spacing: 0.04em;

    color: #000;
  }

  @media (max-width:665px) {
    display: none;
  }
}
