.footer {
  margin-top: 80px;

  @media (max-width:665px) {
    margin-top: 50px;
  }
}

.footer-size {
  @media (max-width:665px) {
    background: url(../images/bgElement360.png) no-repeat center / cover;
  }
}

.footer-background {
  background: url(../images/bgElement.png) no-repeat center / cover;
  width: 50%;

  &__img {
    margin-top: 105px;

    @media (max-width:1139px) {
      margin-top: 55px;
    }
  }

  @media (max-width:1139px) {
    background: url(../images/bgElement960.png) no-repeat center / cover;
  }

  @media (max-width:767px) {
    background: url(../images/bgElement768.png) no-repeat center / cover;
  }

  @media (max-width:665px) {
    background: none;
    width: 0;
  }
}

.footer-text {
  margin-top: 225px;
  margin-left: 30px;

  @media (max-width:665px) {
    margin-top: 121px;
  }

  &__title {
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 72px;
    line-height: 91px;
    letter-spacing: 0.02em;
    color: #000;

    @media (max-width:1139px) {
      font-size: 48px;
      line-height: 60px;
    }

    @media (max-width:767px) {
      font-size: 42px;
      line-height: 53px;
    }
  }

  &__paragraph {
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.04em;
    color: #000;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    margin-top: 30px;
    margin-bottom: 225px;

    @media (max-width:1139px) {
      font-size: 13px;
      line-height: 16px;
      margin-bottom: 162px;
    }

    @media (max-width:767px) {
      margin-bottom: 122px;
    }
  }
}
