.information {
  margin-top: 80px;
  position: relative;

  &__title {
    margin-left: 53%;
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 38px;

    letter-spacing: 0.03em;

    color: #000;

    @media (max-width:1139px) {
      font-size: 24px;
      line-height: 30px;
    }

    @media (max-width: 767px) {
      font-size: 22px;
      line-height: 28px;
    }

    @media (max-width:665px) {
      margin-left: 0;
    }
  }

  &__text {
    margin-top: 50px;
    width: 316px;
    margin-left: 61%;

    font-family: Montserrat, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 132.4%;

    color: #292929;

    @media (max-width:1139px) {
      font-size: 13px;
      line-height: 17px;
      width: 290px;
    }

    @media (max-width: 767px) {
      font-size: 12px;
      line-height: 16px;
      width: 268px;
    }

    @media (max-width:665px) {
      margin: 30px auto;
      width: 270px;
    }
  }

  &__span {
    font-family: CeraPro, sans-serif;
    position: absolute;
    text-transform: uppercase;
    writing-mode: vertical-lr;
    transform: rotate(180deg);
    top: 94px;
    left: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.03em;

    color: #000;

    @media (max-width:665px) {
      top: 170px;
      left: 0;
    }
  }
}
