.container-link {
  margin-top: 225px;
  display: flex;
  margin-bottom: 62px;
  width: 100%;

  @media (max-width:665px) {
    margin-top: 410px;
  }

  &__img {
    margin-left: 15%;
    margin-right: 38%;
    width: 90px;
    display: flex;
    justify-content: space-between;
  }
}

.container-link-phone {
  display: none;

  @media (max-width:665px) {
    margin-top: 410px;
    margin-bottom: 34px;
    width: 80%;
    display: flex;
    justify-content: space-between;
  }
}

.select-bonum {
  display: none;

  @media (max-width:665px) {
    display: block;
    margin-top: 148px;
    margin-left: 10px;
  }
}

.hide {
  @media (max-width:665px) {
    display: none;
  }
}
