.container-colors {

  @media (max-width: 767px) {
    margin-top: 26px;
  }

  @media (max-width:665px) {
    margin-top: 113px;
  }
}

.line-red {
  display: flex;

  &__color-red {
    width: 280px;
    height: 70px;
    background-color: #9a0034;
    margin-left: auto;
    margin-bottom: 45px;

    @media (max-width:1139px) {
      width: 210px;
      height: 51px;
      margin-bottom: 88px;
    }

    @media (max-width: 767px) {
      width: 190px;
      margin-bottom: 76px;
    }

    @media (max-width:665px) {
      width: 133px;
      height: 50px;
      margin-bottom: 50px;
    }
  }
}

.line-ping {
  margin-left: 51%;

  @media (max-width:1139px) {
    margin-bottom: 18px;
  }

  &__color-ping {
    width: 130px;
    height: 60px;
    background-color: #d7969e;
    margin-bottom: 16px;

    @media (max-width:1139px) {
      width: 90px;
      height: 50px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }
  }
}

.line-gray {
  display: flex;
  width: 100%;
}

.position-black-gray {
  margin-left: 32px;

  @media (max-width:665px) {
    position: relative;
    margin-left: 0;
  }

  &__color-black-gray {
    width: 130px;
    height: 60px;
    background-color: #898989;
    margin-bottom: 10px;

    @media (max-width:1139px) {
      width: 80px;
      height: 50px;
      margin-bottom: 15px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }

    @media (max-width:665px) {
      position: absolute;
      top: 65px;
      left: 0;
    }
  }
}

.position-ping-gray {
  margin-left: 51%;
  position: relative;

  &__color-ping-gray {
    width: 130px;
    height: 60px;
    background-color: #e8d8d9;

    @media (max-width:1139px) {
      width: 90px;
      height: 50px;
      position: absolute;
      top: -15px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }

    @media (max-width:665px) {
      top: -102px;
      left: -100px;
    }
  }
}

.position-ligth-gray {
  margin-left: 30px;
  position: relative;

  @media (max-width:665px) {
    margin-left: 10px;
  }

  &__color-ligth-gray {
    width: 130px;
    height: 60px;
    background-color: #dedcdc;

    @media (max-width:1139px) {
      width: 90px;
      height: 50px;
      position: absolute;
      top: -15px;
      left: 90px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }
  }
}

.line-black {
  display: flex;
  position: relative;

  @media (max-width:665px) {
    margin-top: 145px;
    margin-left: 90px;
  }
}

.position-gray {
  margin-left: 197px;

  @media (max-width:665px) {
    margin-left: 0;
  }

  &__color-gray {
    width: 130px;
    height: 60px;
    background-color: #b7b7b7;
    margin-bottom: 10px;

    @media (max-width:1139px) {
      width: 80px;
      height: 50px;
      margin-bottom: 15px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }
  }
}

.position-black {
  margin-left: 30px;

  &__color-black {
    width: 130px;
    height: 60px;
    background-color: #000;

    @media (max-width:1139px) {
      width: 90px;
      height: 50px;
    }

    @media (max-width: 767px) {
      width: 70px;
    }
  }
}

.line-black-position {
  position: absolute;
  top: 22px;
  left: 98%;

  font-family: CeraPro, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  color: #000;

  @media (max-width:1139px) {
    font-size: 14px;
    line-height: 18px;
  }

  @media (max-width:665px) {
    left: 95%;
  }

}

.color-paragraph {
  font-family: CeraPro, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;

  color: #000;
}

.position-text {
  position: absolute;
  top: 52px;
  left: 0;

  @media (max-width:665px) {
    top: -35px;
    left: -100px;
  }
}

.position-text-black-gray {
  @media (max-width:665px) {
    position: absolute;
    top: 127px;
    left: 0;
  }
}
