.colors {
  margin-top: 50px;

  @media (max-width:665px) {
    margin-top: 134px;
  }
}

.colors-information {
  @media (max-width:665px) {
    position: relative;
  }
}

.colors-information-text {
  width: 434px;
  margin-left: 30px;

  @media (max-width:1139px) {
    width: 320px;
  }

  &__title {
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 56px;
    line-height: 70px;
    letter-spacing: 0.03em;
    color: #000;

    @media (max-width:1139px) {
      font-size: 36px;
      line-height: 45px;
      width: 270px;
    }

    @media (max-width: 767px) {
      font-size: 32px;
      line-height: 40px;
    }
  }

  &__paragraph {
    font-family: CeraPro, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;

    text-align: right;

    color: #000;

    @media (max-width:1139px) {
      font-size: 14px;
      line-height: 18px;
    }

    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
}

.colors-information__color {
  margin-bottom: 33px;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  font-family: CeraPro, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.03em;
  color: #000;

  @media (max-width:665px) {
    position: absolute;
    left: 95%;
    top: 120px;
  }
}
