.header {
  background: url(../images/VirnnaOblojkaHome.png) no-repeat center / cover;
}

.container-background {
  background-color: rgba(255, 255, 255, 0.95);
}

.container-background-rigth {
  background-color: rgba(255, 255, 255, 0.99);
}

.container-size {
  max-width: 1140px;
  margin: 0 auto;

  @media (max-width:1139px) {
    max-width: 746px;
  }

  @media (max-width:767px) {
    max-width: 666px;
  }

  @media (max-width:665px) {
    max-width: 350px;
  }
}

.flex {
  display: flex;
  justify-content: space-between;
}
